/* âœ… Style global */
.login-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background-color: #f4f4f4;
}

/* âœ… Barre de titre "Espace Membres RMR-M" */
.login-header {
    width: 100%;
    text-align: center;
    padding: 15px;
    background-color: #0044cc;
    color: white;
    font-size: 20px;
    font-weight: bold;
}

/* âœ… BoÃ®te de connexion centrÃ©e */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

/* âœ… Style de la boÃ®te de dialogue */
.login-box {
    background: white;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    position: relative;
    top: -100px;
    width: 350px;
}
.login-title {
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 15px;
    color: #333; /* Couleur du texte */
}

/* âœ… Champs de saisie */
.login-input {
    width: 90%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}

/* âœ… Bouton de connexion */
.login-button {
    width: 100%;
    padding: 12px;
    background-color: #0044cc;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
}

.login-button:hover {
    background-color: #0033aa;
}

/* âœ… Bouton "Mot de passe oubliÃ© ?" */
.forgot-password {
    background: none;
    border: none;
    color: #0044cc;
    cursor: pointer;
    margin-top: 10px;
    font-size: 14px;
}

.forgot-password:hover {
    text-decoration: underline;
}

/* âœ… BoÃ®te de dialogue pour la rÃ©initialisation */
.reset-dialog {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

/* âœ… Contenu de la boÃ®te de dialogue */
.reset-box {
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 350px;
}

/* âœ… Bouton d'annulation */
.cancel-button {
    background: #ccc;
    color: black;
    border: none;
    padding: 10px;
    margin-top: 10px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    width: 100%;
}

.cancel-button:hover {
    background: #bbb;
}