.member-details-container {
    max-width: 500px;
    margin: 40px auto;
    padding: 20px;
    background: white;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
}

.member-details-container h2 {
    color: #333;
    margin-bottom: 10px;
}

.member-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

/* Agir sur les champs */
.form-group {
    display: flex;
    flex-direction: column;
    align-items: center; /* Centrer les champs dans le conteneur */
    width: 100%;
    margin-bottom: 5px; /* RÃ©duit l'espace entre les champs */
}

.form-group label {
    font-weight: bold;
    margin-bottom: 5px;
}

.form-group input,
.form-group select {
    width: 80%; /* Les champs prennent toute la largeur du conteneur */
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
}

.form-group input:focus,
.form-group select:focus {
    border-color: #007bff;
    outline: none;
}

.button-container {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.btn-save {
    background-color: #28a745;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s;
}

.btn-save:hover {
    background-color: #218838;
}

.btn-cancel {
    background-color: #dc3545;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s;
}

.btn-cancel:hover {
    background-color: #c82333;
}

/* âœ… Responsiveness pour Tablettes */
@media (max-width: 768px) {
    .member-details-container {
        width: 90%;
    }
}

.button-container {
    display: flex;
    flex-direction: column; /* âœ… Aligner les boutons verticalement */
    gap: 10px; /* âœ… Ajouter un espace entre chaque bouton */
    align-items: center; /* âœ… Centrer les boutons */
    margin-top: 10px;
}

.btn-save, .btn-cancel {
    width: 80%; /* âœ… Les boutons prennent toute la largeur */
    padding: 12px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s;
}

.btn-save {
    background-color: #28a745;
    color: white;
}

.btn-save:hover {
    background-color: #218838;
}

.btn-cancel {
    background-color: #dc3545;
    color: white;
}

.btn-cancel:hover {
    background-color: #c82333;
}

/* âœ… Responsiveness pour Tablettes */
@media (max-width: 768px) {
    .member-details-container {
        width: 90%;
    }

    .button-container {
        width: 100%;
    }
}

